<template>
  <div v-if='show' class="picket-fixed">
    <div class="type-picket phone-pad">
      <div class='flex space_between bar'>
        <img @click.stop='onCancel' class="" src="../../assets/img/face/icon_back.png"/>
        <div @click.stop="onConfirm">确定</div>
      </div>
      <!-- visible-item-count=6 :swipe-duration='1000' item-height=50 -->
      <div class="main">
        <van-picker
          :columns="item"
          @confirm="onConfirm"
          @cancel="onCancel"
          @change="onChange"
          item-height=50
          swipe-duration=100
          visible-item-count=5
        />
      </div>
    </div>
  </div>
</template>

<script>
  import Picker from 'vant/lib/picker';
  import 'vant/lib/picker/style';
  
  export default{
    name:'Picker',
    components:{
      [Picker.name]:Picker,
    },
    data() {
      return {
        // columns: ['杭州', '宁波', '温州', '绍兴', '湖州', '嘉兴', '金华', '衢州'],
        show:false,
        index:0,
        pickerWhat:0,
      };
    },
    props:{
      item:{
        type:Array,
        default(){
          return ['居民身份证','护照','台湾居民往来通行证','港澳居民往来通行证']
        },
      },
    },
    methods: {
      pickerShow(pickerWhat){
        if(typeof pickerWhat==='object'){
          pickerWhat=0
        }
        this.show=true
        this.pickerWhat=pickerWhat;//pickerWhat=>0 选择证件类型 pickerWhat=>1 性别
      },
      onConfirm() {
        console.log(`onConfirm当前值：${this.index}`);
        this.show=false;
        this.$emit('getPicker',{index:this.index,pickerWhat:this.pickerWhat,checkData:this.item[this.index]})
        this.index=0
      },
      onChange(picker, value, index) {
        console.log(`onChange当前值：${value}, 当前索引：${index}`);
        this.index=index;
        this.$nextTick(()=>{
          $('li.van-picker-column__item--selected').prev().css({'color':'rgba(100,100,105,0.60)'});
        })
      },
      onCancel() {
        console.log('取消');
        this.index=0;
        this.show=false;
      },
    },
  }
</script>

