<template>
  <div class="writeMes box">
    <!--手持证件认证-->
    <div class="item flex space_between">
      <div class="item-l font-bold">手机号</div>
      <div class='item-r hasTextCol'>{{mobile}}</div>
    </div>
    <div class="item flex space_between">
      <div class="item-l font-bold">选择证件</div>
      <div class='item-r flex' @click.stop="choosePicker(0)">
        <div class="hasTextCol">{{tempCardName ? tempCardName : "居民身份证"}}</div>
        <img src="../../assets/img/addCar/click.png"/>
      </div>
    </div>
    <div class="item flex space_between">
      <div class="item-l font-bold">真实姓名</div>
      <input v-model='uploadJson.name' @input="nameLen" class='item-input tr' placeholder="请输入姓名" />
    </div>
    <div class="item flex space_between">
      <div class="item-l font-bold">证件号</div>
      <input v-model='uploadJson.authNumber' @input="authNumberLen" class='item-input tr' placeholder="请输入证件号" />
    </div>
    <div v-if="tempCardType==0" class="item flex space_between">
      <div class="item-l font-bold">地址</div>
      <input v-model='uploadJson.address' class='item-input tr' placeholder="请输入身份证地址" />
    </div>
    <div class="item flex space_between">
      <div class="item-l font-bold">证件起始日期</div>
      <div class='item-r flex' @click.stop="dateDialogShow('0')">
        <div :class="(startTime ? 'hasTextCol':'')">{{startTime ? startTime : "请选择"}}</div>
        <img src="../../assets/img/addCar/click.png"/>
      </div>
    </div>
    <div class="item flex space_between">
      <div class="item-l font-bold">长期证件</div>
      <img @click.stop='isLong=true;' v-if='!isLong' class="time-status" src="../../assets/img/face/tine-close.png"/>
      <img @click.stop='isLong=false;' v-else class="time-status" src="../../assets/img/face/time-open.png"/>
    </div>
    <div v-if="!isLong" class="item flex space_between">
      <div class="item-l font-bold">证件截止日期</div>
      <div class='item-r flex' @click.stop="dateDialogShow('1')">
        <div :class="(endTime ? 'hasTextCol':'')">{{endTime ? endTime:"请选择"}}</div>
        <img src="../../assets/img/addCar/click.png"/>
      </div>
    </div>
    <div v-if="tempCardType!=0" class="item flex space_between">
      <div class="item-l font-bold">性别</div>
      <div class='item-r flex' @click.stop='choosePicker(1)'>
        <div :class="(chooseSex[checkSex] ? 'hasTextCol':'')">{{chooseSex[checkSex] ? chooseSex[checkSex] : "请选择"}}</div>
        <img src="../../assets/img/addCar/click.png"/>
      </div>
    </div>
    <div v-if="tempCardType!=0" class="item flex space_between">
      <div class="item-l font-bold">出生日期</div>
      <div class='item-r flex' @click.stop="dateDialogShow('2')">
        <div :class="(birthDay ? 'hasTextCol':'')">{{birthDay ? birthDay:'请选择'}}</div>
        <img src="../../assets/img/addCar/click.png"/>
      </div>
    </div>
    <div class="item flex space_between noBorder">
      <div class="item-l font-bold">上传手持证件照照片</div>
      <div class='item-r item-r-img' @click.stop='exampleShowFn'>查看示例图</div>
    </div>
    <div class="flex upload-img space_between tc">
      <div @click.stop='openUploadDialog(1,"",tempCardType,true)' :key="('img1_'+uploadImg1.localImg)" class="upload-img1 bgCss" :style="{'background-image':'url('+(uploadImg1.res ? uploadImg1.localImg : upload1)+')'}">
        <div class="no-upload" v-if="!uploadImg1.localImg">
          <img src="../../assets/img/addCar/add.png"/>
          <div>上传手持人像面</div>
        </div>
        <div v-else class="reset-upload">重新上传</div>
      </div>
      <div @click.stop='openUploadDialog(2,"",tempCardType,tempCardType==0 ? false:true,getUploadMesApp2)' :key="('img2_'+uploadImg2.localImg)" class="upload-img2 bgCss" :style="{'background-image':'url('+(uploadImg2.res ? uploadImg2.localImg : upload2)+')'}">
        <div class="no-upload" v-if="!uploadImg2.res">
          <img src="../../assets/img/addCar/add.png"/>
          <div>上传国徽面</div>
        </div>
        <div v-else class="reset-upload">重新上传</div>
      </div>
    </div>
    <div class="submit tc phone-pad">
      <div class="submit-text">信息仅用于实名认证，我们将严格保护您的数据和隐私安全</div>
      <div @click.stop='submitBtn' class="btn font-bold">
        <div :class="hasColBtn ? 'check':''">提交</div>
      </div>
    </div>
    <!--查看示例图-->
    <div v-if="exampleShow" class="example hiddenbg">
      <div class="example-main">
        <img @click.stop='exampleShow=false;' class="close" src="../../assets/img/face/close.png"/>
        <div class="example-title tc font-bold">查看示例图</div>
        <div class="example-text tl">
          1. 请保证图片焦点清晰，信息可读，不可太小或模糊 不清<br/>
          2. 不可有反光影响文字显示<br/> 
          3. 请让四个边角完整展示，不要用手遮挡证件<br/>  
          4. 请不要自行加水印<br/>  
          5. 请不要使用扫描件或复印件<br/> 
        </div>
        <img class="example-img" src="../../assets/img/face/example.png"/>
      </div>
    </div>
    <!--退出二次提醒-->
    <div v-if='noticeShow' class="credentials-bg hiddenbg tc">
      <div class="credentials-bg-main hiddenbg_div">
        <div class="credentials-bg-top">
          <div v-if='noticeStatus==12 || noticeStatus==9' class="text3">{{noticeMes}}</div>
          <div v-if="noticeStatus==11" class="text2 tl">
            1.请保证图片焦点清晰，信息可读,不可太小或模糊不清<br/>
            2.不可有反光影响文字显示<br/> 
            3.请让四个边角完整展示，不要用手遮挡证件<br/> 
            4.请不要自行加水印<br/> 
            5.请不要使用扫描件或复印件<br/>
          </div>
          <div v-if='noticeStatus==0' class="text3">退出后本次编辑将不被保存<br/>是否确认退出</div>
          <div v-if='noticeStatus==1 || noticeStatus==2' class="text1">{{noticeStatus==1 ? '认证失败' : noticeStatus==2 ? '提交成功' : ''}}</div>
          <div v-if='noticeStatus==1 || noticeStatus==2' class="text2">{{noticeStatus==1 ? '暂不支持14周岁以下的用户进行实名认证' : noticeStatus==2 ? '我们将尽快审核您的资料' : ''}}</div>
        </div>
        <div class="flex font-bold toast-mybtn">
          <div @click.stop='noticeShow=false;' v-if="noticeStatus==11" class="munual-btn1">重新识别证件</div>
          <div v-if='noticeStatus==0' @click.stop='comfirCancel' class="munual-btn1">取消</div>
          <div v-if='noticeStatus==9' @click.stop='isHideDialog' class="munual-btn1">返回</div>
          <div v-if='noticeStatus==0' class="line"></div>
          <div v-if='noticeStatus==0' @click.stop="comfirmSure" class="munual-btn2">确定</div>
          <div @click.stop='clickHandler' v-if='noticeStatus==1 || noticeStatus==2 || noticeStatus==12' class="munual-btn2">我知道了</div>
          <div @click.stop="loginPage" v-if="noticeStatus==9" class="munual-btn2">切换帐号</div>
        </div>
      </div>
    </div>
    <!--选择日期-->
    <DatetimePicker :startTime='startTime' ref="date" @getDate="getDate"></DatetimePicker>
    <!--选择身份类型 选择性别-->
    <Picker ref="Picker" :item='pickerItem' @getPicker='getPicker'></Picker>
    <!--选择本地图片-->
    <uploadImage ref="uploadImage" @localImage='localImage' :uploadIndex="uploadIndex"></uploadImage>
  </div>
</template>

<script>
  import upload1 from '../../assets/img/certification/upload1.png';
  import upload2 from '../../assets/img/certification/upload2.png';
  // import DatetimePicker from '@/DatetimePicker/index.vue'
  import datePicker from 'mixins/datePicker.js'
  // import Picker from '@/Picker/index.vue'
  import pickerMixins from 'mixins/pickerMixins.js'
  import uploadImgMixins from 'mixins/uploadImgMixins.js'
  import inputLen from 'mixins/inputLen.js'
  
  import {transferBuickFansFromUser} from 'fetch/api.js'
  import bridge from 'fetch/bridge.js'
  let Base64 = require('js-base64').Base64
  export default{
    name:'writeMes',
    mixins:[datePicker,pickerMixins,uploadImgMixins,inputLen],
    beforeRouteLeave(to, from, next){
      console.log('beforeRouteLeave===')
      if(this.comfirmBtn){
        //if(!this.toastPush){
          this.noticeShow=true
          this.noticeStatus=0;
          this.comfirmTwo=next;
        //   history.pushState(null, null, this.myurl);
        // }else{
        //   next()
        // }
      }else{
        next()
      }
    },
    data(){
      return {
        toastPush:false,
        myurl:null,
        comfirmTwo:null,
        upload1,
        upload2,
        uploadImg1:{url:'',res:false,localImg:''},
        uploadImg2:{url:'',res:false,localImg:''},
        writeJson:{
          cardType:0,//选择证件 1=>护照
        },
        chooseSex:['男','女'],
        chooseCardType:['居民身份证','护照','台湾居民往来通行证','港澳居民往来通行证'],
        pickerItem:[],
        exampleShow:false,
        noticeShow:false,
        /*0=>退出二次提醒 1=>14周岁之下 2=>提交成功*/
        noticeStatus:-1,
        isLong:false,//是否是长期
        mobile:'',
        uploadJson:{
          name:'',
          authType:1,
          authNumber:'',
          authPassType:99,
          authPassToken:'',
          imageId:'',
          frontImgUrl:'',
          backImgUrl:'',
          expireDate:'',
          holdImgUrl:'',
          gender:'1',
          birth:'',
          address:'',
        },
        preventRes:false,
        noticeMes:'',
        type:'',
      }
    },
    created(){
      this.mobile=localStorage.getItem('mobile')
      this.type=this.$route.query.type;
      console.log('人工审核页面===this.type=',this.type)
      // setTimeout(()=>{
      //   bridge('finishPage')
      // },2000)
      
    },
    activated(){
      this.toastPush=false;
      this.myurl = window.location.href;
      this.onGetImgKey=true;
      this.tempCardName=decodeURIComponent(this.$route.query.typeName)
      this.tempCardType=this.getCardType(this.tempCardName)
      this.clearData();//清除数据
      let {img1,img2,postData,isLong}=this.$store.state.certCardMes;//三要素的图片信息
      console.log('获取到的图片信息=',{img1,img2,postData,isLong},'当前页面=',this.$route)
      if(img1){
        this.uploadImg1=img1
        if(img1.name){
          this.uploadJson.name=img1.name
        }
        if(img1.id_number){
          this.uploadJson.authNumber=img1.id_number
        }
        if(img1.address){
          this.uploadJson.address=img1.address
        }else{
          this.uploadJson.address='';
        }
        this.uploadImg1.localImg=''
        this.uploadImg1.url=''
        this.uploadImg1.res=false;
        this.uploadImg1.file='';
      }else{
        this.uploadImg1={url:'',res:false,localImg:''};
      }
      if(img2){
        this.uploadImg2=img2
        this.startTime=this.uploadImg2.startTime
        this.endTime=this.uploadImg2.endTime
      }else{
        this.uploadImg2={url:'',res:false,localImg:''};
      }
      if(postData){
        this.uploadJson={...postData,authPassType:99}
      }
      this.isLong=isLong ? isLong:false;
      console.log('this.uploadImg1=',this.uploadImg1,'this.uploadImg2=',this.uploadImg2,isLong,this.isLong)
      // this.noticeStatus=12;
      // this.noticeShow=true;//测试
      // this.transferBuickFansFromUserCall({
      //   resultCode:'1151',
      //   message:'其他错误提示',
      // })
      // return
    },
    computed:{
      comfirmBtn(){
        let {name,authNumber}=this.uploadJson
        let startTime=this.startTime;
        let isLong=this.isLong
        let endTime=this.endTime;
        let checkSex=this.checkSex
        let birthDay=this.birthDay;
        let img1=this.uploadImg1.localImg
        let img2=this.uploadImg2.res
        let res=name.length>=2 && authNumber && startTime && img1 && img2;
        if(name.length || authNumber || startTime || img1 || img2 || endTime || birthDay){
          return true;
        }
        return false;
      },
      hasColBtn(){
        let {name,authNumber,address}=this.uploadJson
        let startTime=this.startTime;
        let isLong=this.isLong
        let endTime=this.endTime;
        let checkSex=this.checkSex
        let birthDay=this.birthDay;
        let img1=this.uploadImg1.localImg
        let img2=this.uploadImg2.res
        let res=name.length>=2 && authNumber && startTime && img1 && img2;
        //console.log('颜色=',name , authNumber , startTime , img1 ,img2)
        if(!this.isLong && this.tempCardType==0){
          //console.log('000=',endTime)
          return res && endTime && address;
        }else{
          if(this.tempCardType!=0){
            //console.log('111=',res,checkSex!=-1 , birthDay)
            return res && checkSex!=-1 && birthDay
          }else{
            //console.log('222=')
            return res && address;
          }
        }
      },
    },
    methods:{
      authAction(eventName){//埋点
        if(this.$route.query.test==1 || this.type=='app'){
          try{
            bridge('authAction',{eventName})
          }catch(err){
            console.log('埋点失败--',eventName)
          }
        }
      },
      isHideDialog(){
        this.noticeShow=false;
        this.noticeStatus=-1;
        this.noticeMes='';
        this.authAction('my_realname_submit_back')
      },
      loginPage(){
        this.authAction('my_realname_submit_change')
        bridge('closeAndGotoLoginPage')
      },
      authAction(eventName){//埋点
        if(this.$route.query.test==1 || this.type=='app'){
          try{
            bridge('authAction',{eventName})
          }catch(err){
            console.log('埋点失败--',eventName)
          }
        }
      },
      exampleShowFn(){
        this.exampleShow=true;
        this.authAction("my_realname_watch");
      },
      clearData(){//清除数据
        this.exampleShow=false
        this.noticeShow=false
        this.isLong=false;
        let img={url:'',res:false,localImg:''}
        this.uploadImg1={...img}
        this.uploadImg2={...img}
        this.uploadJson={
          name:'',
          authType:1,
          authNumber:'',
          authPassType:99,
          authPassToken:'',
          imageId:'',
          frontImgUrl:'',
          backImgUrl:'',
          expireDate:'',
          holdImgUrl:'',
          gender:'1',
          birth:'',
          address:'',
        }
        this.preventRes=false;
      },
      async submitBtn(){//提交
        if(!this.hasColBtn) return;
        if(this.preventRes) return;
        this.preventRes=true
        let params={...this.uploadJson}
        let time=''
        if(!this.isLong){
          time=this.startTime.replace(/-/g,'.')+'-'+this.endTime.replace(/-/g,'.')
        }else{
          time=this.startTime.replace(/-/g,'.')+'-长期'
        }
        params.name=Base64.encode(params.name)
        params.authNumber=Base64.encode(params.authNumber)
        params.address=Base64.encode(params.address)
        params.expireDate=time
        params.gender=parseFloat(this.checkSex)+1;
        params.birth=this.birthDay
        params.frontImgUrl=this.uploadImg1.url
        params.holdImgUrl=params.frontImgUrl;
        params.backImgUrl=this.uploadImg2.url;
        params.authType=this.getCardType(this.tempCardName);
        if(params.authType==0){
          params.authType=1;
          params.gender=''
        }
        if(this.type=='app'){
          bridge(
          "receiveLoadNetworkArgument",
          {
            path:'userAuth/transferBuickFansFromUser',gateway:"private",params:params,method:"POST"
          },
          {
            callback:this.transferBuickFansFromUserCall,winName:'acceptNewEnergyResultSet'
          }
          );
        }else{
          let res=await transferBuickFansFromUser(params)
          this.transferBuickFansFromUserCall(res)
        }
      },
      transferBuickFansFromUserCall(res){
        this.preventRes=false;
        console.log('res=',res)
        this.noticeShow=true;
        let code=res.resultCode
        if(res && code=='0000'){
          this.noticeStatus=2;//提交成功
        }else{//报错
          this.noticeMes=res.message;
          if(code=='1151'){
            if(this.$route.query.type=='app'){
              this.noticeStatus=9;
            }else{
              this.noticeStatus=12;
            }
          }else{
            this.noticeStatus=12;
          }
          
          
        }
      },
      getCardType(name){
        switch(name){
          case '居民身份证':
            return 0;
          case '护照':
            return 4;
          case '台湾居民往来通行证':
            return 3;
          case '港澳居民往来通行证':
            return 2;
        }
      },
      getUploadMes(res,uploadIndex,tempCarType){//上传证件文件
        console.log('上传证件文件0000=',res,uploadIndex,'tempCarType=',tempCarType)
        if(res.resultCode=='0000'){
          let {key,data}=res.data;
          if(uploadIndex==1){
            this.uploadImg1.url=key
            this.uploadImg1.res=true
          }else if(uploadIndex==2){
            if(tempCarType==0){
              let mes=JSON.parse(Base64.decode(data));
              let {validity}=mes
              //validity='';//测试
              this.uploadImg2.validity=validity;
              if(validity){
                let time=validity.split("-");
                this.startTime=time[0].replace(/\./g,'-');
                this.endTime=time[1].replace(/\./g,'-');
              }else{
                this.noticeStatus=11;
                this.noticeShow=true;
                this.startTime='';
                this.endTime='';
                // this.uploadImg2.res=false;
                // this.uploadImg2.url='';
              }
            }
            this.uploadImg2.res=true;
            this.uploadImg2.url=key;
          }
        }else{
          let img=this.uploadImg1;
          this.noticeShow=true;
          if(uploadIndex==2){
            if(tempCarType==0){
              img=this.uploadImg2;
              this.startTime='';
              this.endTime='';
              this.noticeStatus=11
            }else{
              this.noticeStatus=12;
              this.noticeMes=res.message;
            }
            img=this.uploadImg2;
          }else{
            this.noticeStatus=12;
            this.noticeMes=res.message;
          }
          img.url=''
          img.res=false;
        }
      },
      choosePicker(val){
        if(val==0){
          this.pickerItem=this.chooseCardType
        }else if(val==1){
          this.pickerItem=this.chooseSex
        }
        this.pickerShow(val)
      },
      gotoSuccess(){
        this.$router.replace({name:'faceSuccess'})
      },
      clickHandler(){
        let status=this.noticeStatus
        this.noticeShow=false;
        this.noticeStatus=-1;
        if(status==2){
          //关闭所有页面
          if(this.type=='app'){//JoinSuccess
            bridge('JoinSuccessAndClosePage');
            //bridge('JoinSuccess',{needClose:'1'})
            //bridge('finishPage')
          }else{
			  if(sessionStorage.isminiType=='wechatMini'){
			    //wx.miniProgram.navigateTo({ url: localStorage.getItem('myhref')});
				wx.miniProgram.navigateBack()
			  }else{
			    window.location.replace(localStorage.getItem('myhref'))
			  }
            // if (typeof WeixinJSBridge==='object'){
            //   document.addEventListener('WeixinJSBridgeReady', function(){ WeixinJSBridge.call('closeWindow'); }, false);
            //   WeixinJSBridge.call('closeWindow');
            // }else{
            //   window.opener=null;
            //   window.open('about:blank','_self','').close()
            // }
          }
        }
      },
      comfirmSure(){
        if(this.noticeStatus==0){
          this.comfirmTwo();
          this.toastPush=true;
        }
        //this.comfirmTwo=null;
        this.noticeShow=false;
        this.noticeStatus=-1;
       // this.$router.back(-1);
      },
      comfirCancel(){
        this.noticeShow=false;
        this.noticeStatus=-1;
        this.toastPush=true;
        history.pushState(null, null, this.myurl);
        //this.$router.back(-1);
        //this.comfirmTwo=null;
      },
    }
  }
</script>

<style scoped lang='scss'>
  @import '~css/noticeToast.scss';
  .writeMes{
    padding:24px 32px 0;
    .item{
      height: 112px;
      line-height:112px;
      font-size: 28px;
      color: #3d3d41;
      border-bottom:1px solid #ebeced;
      .item-r{
        color: #d7d7d7;
        .hasTextCol{
          color: #3d3d41;
        }
        >img{
          width:32px;
          margin-left:16px;
        }
      }
      >.time-status{
        width: 92px;
      }
      input:-ms-input-placeholder {
          color: #d7d7d7;
      }
      
      input::-webkit-input-placeholder {
          color:#d7d7d7;
      }
      
      input::-moz-placeholder {
          color: #d7d7d7;
      }
      
      input:-moz-placeholder {
          color: #d7d7d7;
      }
      .item-input{
        border:0;
        height: 40px;
        font-size: 28px;
        color: #d7d7d7;
        line-height: 40px;
        color: #3d3d41;
        width:500px;
      }
      .item-r-img{
        color: #1e5dd1;
      }
    }
    .noBorder{
      border:0;
    }
    
    .upload-img{
      width:622px;
      margin:0 auto;
      padding-bottom:272px;
      >div{
        width: 296px;
        height: 188px;
        position:relative;
        background-position:center center;
        border-radius: 16px 16px 16px 16px;
        box-shadow: 0 4px 16px 0 rgba(0,0,0,.06);
        .no-upload{
          position:absolute;
          top:50%;
          left:50%;
          transform:translate(-50%,-50%);
          white-space: nowrap;
          img{
            width:80px;
            margin-bottom:8px;
          }
          div{
            font-size: 26px;
            color: #94949c;
            line-height: 38px;
          }
        }
        .reset-upload{
          width:100%;
          height: 68px;
          line-height:68px;
          background: rgba(255,255,255,0.40);
          border-radius: 0px 0px 16px 16px;
          backdrop-filter: blur(32px);
          position:absolute;
          bottom:0;
          left:0;
          font-size:26px;
        }
      }
    }
    
