export default{
  methods:{
    nameLen(){//姓名：15个字
      let name=this.uploadJson.name.trim();
      if(name.length>50){
        name=name.substring(0,50)
      }
      this.uploadJson.name=name;
    },
    authNumberLen(){//证件号
      let num=this.uploadJson.authNumber.trim();
      if(num.length>18){
        num=num.substring(0,18)
      }
      this.uploadJson.authNumber=num;
    },
  }
}