import Picker from '@/Picker/index.vue'
export default{
  data(){
    return{
      errorType:-1,
      errorShow:false,
      tempCardType:0,//临时证件类型
      tempCardName:'',//临时证件类型数据
      CardType:0,//证件类型
      checkSex:-1,//性别
      errorTextMes:'',//其他错误信息
    }
  },
  components:{
    Picker,
  },
  methods:{
    pickerShow(pickerWhat=0){//pickerWhat=>0 选择证件类型 pickerWhat=>1 性别
      this.$refs.Picker.pickerShow(pickerWhat)
    },
    getPicker(data){//或者到用户选择的证件
      let {index,pickerWhat,checkData}=data
      console.log('或者到用户选择的证件=',data,'pickerWhat=',pickerWhat,index)
      if(pickerWhat==0){//pickerWhat=>0 选择证件类型
        this.tempCardType=index
        this.tempCardName=checkData
        if(this.$route.meta.clear){
          console.log('清除数据===')
          this.clearData && this.clearData();//清除数据
        }
      }else if(pickerWhat==1){//pickerWhat=>1 性别
        this.checkSex=index
      }
    },
  }
}